import React, { useState } from 'react';
import {
  Box,
  Button,
  FormControl,
  FormLabel,
  Input,
  VStack,
  useToast,
  Select,
  NumberInput,
  NumberInputField,
  NumberInputStepper,
  NumberIncrementStepper,
  NumberDecrementStepper,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalBody,
  useDisclosure,
} from '@chakra-ui/react';
import RegistrationSuccess from './RegistrationSuccess';

const RegisterForm = () => {
  const [formData, setFormData] = useState({
    chineseName: '',
    englishName: '',
    birthYear: '',
    gender: '',
    idStatus: '',
    economicStatus: '',
    phone: '',
    address: '',
    residenceType: '',
    maritalStatus: '',
  });
  const [isLoading, setIsLoading] = useState(false);
  const [registeredMemberId, setRegisteredMemberId] = useState('');
  const toast = useToast();
  const { isOpen, onOpen, onClose } = useDisclosure();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleNumberChange = (name, value) => {
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const response = await fetch('http://localhost:5000/api/members/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      const data = await response.json();

      if (!response.ok) {
        if (response.status === 409) {
          toast({
            title: '註冊失敗',
            description: '該會員已經存在',
            status: 'error',
            duration: 3000,
            isClosable: true,
          });
        } else {
          throw new Error(data.message || '註冊失敗');
        }
        return;
      }

      setRegisteredMemberId(data.memberId);
      onOpen();

      // 清空表单
      setFormData({
        chineseName: '',
        englishName: '',
        birthYear: '',
        gender: '',
        idStatus: '',
        economicStatus: '',
        phone: '',
        address: '',
        residenceType: '',
        maritalStatus: '',
      });
    } catch (error) {
      toast({
        title: '錯誤',
        description: error.message,
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleSuccessClose = () => {
    onClose();
    setRegisteredMemberId('');
  };

  return (
    <>
      <Box as="form" onSubmit={handleSubmit} width="100%" maxW="600px">
        <VStack spacing={4}>
          <FormControl isRequired>
            <FormLabel>中文姓名</FormLabel>
            <Input
              name="chineseName"
              value={formData.chineseName}
              onChange={handleChange}
              placeholder="請輸入中文姓名"
            />
          </FormControl>

          <FormControl>
            <FormLabel>英文姓名</FormLabel>
            <Input
              name="englishName"
              value={formData.englishName}
              onChange={handleChange}
              placeholder="請輸入英文姓名"
            />
          </FormControl>

          <FormControl isRequired>
            <FormLabel>出生年份</FormLabel>
            <NumberInput
              value={formData.birthYear}
              onChange={(value) => handleNumberChange('birthYear', value)}
              min={1900}
              max={new Date().getFullYear()}
            >
              <NumberInputField placeholder="請輸入出生年份" />
              <NumberInputStepper>
                <NumberIncrementStepper />
                <NumberDecrementStepper />
              </NumberInputStepper>
            </NumberInput>
          </FormControl>

          <FormControl isRequired>
            <FormLabel>性別</FormLabel>
            <Select
              name="gender"
              value={formData.gender}
              onChange={handleChange}
              placeholder="請選擇性別"
            >
              <option value="男">男</option>
              <option value="女">女</option>
            </Select>
          </FormControl>

          <FormControl isRequired>
            <FormLabel>身份狀況</FormLabel>
            <Select
              name="idStatus"
              value={formData.idStatus}
              onChange={handleChange}
              placeholder="請選擇身份狀況"
            >
              <option value="永久身份證">永久身份證</option>
              <option value="單程證">單程證</option>
              <option value="雙程證">雙程證</option>
            </Select>
          </FormControl>

          <FormControl isRequired>
            <FormLabel>經濟狀況</FormLabel>
            <Select
              name="economicStatus"
              value={formData.economicStatus}
              onChange={handleChange}
              placeholder="請選擇經濟狀況"
            >
              <option value="低收入家庭">低收入家庭</option>
              <option value="綜援家庭">綜援家庭</option>
              <option value="無收入家庭">無收入家庭</option>
              <option value="其他">其他</option>
            </Select>
          </FormControl>

          <FormControl>
            <FormLabel>手提電話</FormLabel>
            <Input
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              placeholder="請輸入手提電話"
            />
          </FormControl>

          <FormControl isRequired>
            <FormLabel>地址</FormLabel>
            <Input
              name="address"
              value={formData.address}
              onChange={handleChange}
              placeholder="請輸入地址"
            />
          </FormControl>

          <FormControl isRequired>
            <FormLabel>居住類型</FormLabel>
            <Select
              name="residenceType"
              value={formData.residenceType}
              onChange={handleChange}
              placeholder="請選擇居住類型"
            >
              <option value="劏房">劏房</option>
              <option value="公屋">公屋</option>
            </Select>
          </FormControl>

          <FormControl isRequired>
            <FormLabel>婚姻狀況</FormLabel>
            <Select
              name="maritalStatus"
              value={formData.maritalStatus}
              onChange={handleChange}
              placeholder="請選擇婚姻狀況"
            >
              <option value="單身">單身</option>
              <option value="已婚">已婚</option>
              <option value="離婚/分居">離婚/分居</option>
            </Select>
          </FormControl>

          <Button
            type="submit"
            colorScheme="blue"
            width="100%"
            isLoading={isLoading}
          >
            註冊會員
          </Button>
        </VStack>
      </Box>

      <Modal isOpen={isOpen} onClose={handleSuccessClose} isCentered>
        <ModalOverlay />
        <ModalContent>
          <ModalBody p={6}>
            <RegistrationSuccess
              memberId={registeredMemberId}
              onClose={handleSuccessClose}
            />
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  );
};

export default RegisterForm; 